import React, { Component } from 'react';
import ArticalConfig from './articalConfig';
import { Icon, Input, Button, Form } from 'antd';

import {Link } from 'react-router-dom'
import PageConfig from './config';
import eventProxy from './eventProxy';
import ArticalListsConfig from './articalListsConfig';

import './page.css';
class ArticalPage extends React.Component{

   submit=()=>{
       const value = this.props.form.getFieldsValue();
    
   }
    render(){
        
        const {getFieldDecorator} = this.props.form;
        var info= ArticalListsConfig[this.props.match.params.id] ;
        const page = PageConfig[info.type2]['title'];
        eventProxy.trigger('changePageIndex',info.type1,info.type2,'j')
        const formItemLayout = {
            labelCol: {
              span: 3 
            },
            wrapperCol: {
              span: 10 
            },
          };
        return(
            <div style={{width:'70%',marginTop:'20px',float:'left'}}>

                <div>
                     {info.title}
                </div>
                
                <Icon style={{clear:'both'}} type='user'/>
                {info.writer}
                       
                <Icon type='folder-open' style={{marginLeft:'30px'}}/>
                <a> <Link to={'/articalpage/'+info.type1+"/"+info.type2}>{page} </Link></a>
                
                <hr/>

                <div>
                    {ArticalConfig[info.id]}
                </div>   


                <div className='messageBox'>
                     <label style={{fontSize:'25px'}}>留下你的回复</label>
                     <div className='messageLine'>你的email不会被公开</div>
                
                     <Form onSubmit={this.submit}>
                          <Form.Item>
                              {getFieldDecorator('message',{
                                  rules:[]
                              })
                              (<Input style={{height:'100px'}}/>)
                               }
                          </Form.Item>

                          <Form.Item  {...formItemLayout} label='姓名'>
                              {getFieldDecorator('name',{
                                  rules:[]
                              })
                              (<Input />)
                               }
                          </Form.Item>
                          <Form.Item  {...formItemLayout} label='地址'>
                              {getFieldDecorator('address',{
                                  rules:[]
                              })
                              (<Input />)
                               }
                          </Form.Item>
                          <Form.Item  {...formItemLayout} label='邮件'>
                              {getFieldDecorator('email',{
                                  rules:[]
                              })
                              (<Input />)
                               }
                          </Form.Item>

                          <Button  onClick={this.submit}>提交</Button>
                     </Form>
                

                </div>
            </div>
        )
    }
} (Form.create({ name: 'register' })(ArticalPage));
export default (Form.create({ name: 'register' })(ArticalPage));
