import React, { Component } from 'react';
import ArticalListsConfig from './articalListsConfig';

import './page.css';
export default class FileList extends React.Component{
    render(){

        return(
            <div style={{width:'70%',float:'left'}}>
                <div>
                    文章归档
                </div>
                <div >
                    <span>折叠</span>
                    <ul id='fileListDiv'>
                   
                    </ul>
                    </div>
            </div>
        )
    }
    componentDidMount(){
        var div =document.getElementById('fileListDiv');
        var pretime='';
        var filetitle;
        for(var infokey in ArticalListsConfig){
            var info =ArticalListsConfig[infokey];
            var timeInfo = info.time.split('-');
            if(pretime!=timeInfo[0]+'-'+timeInfo[1]){
                div.innerHTML+="<li ><span class=\"timer\">"+timeInfo[0]+"年"+timeInfo[1]+"月</span></li>";
                filetitle= document.createElement('ul');

             div.append(filetitle);
            }
            filetitle.innerHTML+="<li><a href=http://localhost:3000/artical/"+info.id+">"+info.title+"</a></li>";
            pretime=timeInfo[0]+'-'+timeInfo[1];
        }


    }
}