import React, { Component } from 'react';
import { Layout, Row, Col, Input, Button, Carousel, Icon } from 'antd';
import headpng from '../image/head.png';
import emailpng from '../image/email.png';

import messagepng from '../image/message.png';
import weibopng from '../image/weibo.png';
import zhifubaopng from '../image/zhifubao.png';
import mybook from '../image/mybook.jpg';

import './page.css';

import { BrowserRouter as Router,Route ,Switch} from 'react-router-dom'
import ArticalList from './articalList';
import ConnectMe from './connectme';
import PageContent from './pageContent';
import ArticalPage from './articalPage';
import MyBook from './mybook';
import RightContent from './rightContent';
import FileList from './fileList';

const { Header, Footer, Sider, Content  } = Layout;
export default class BlogPage extends React.Component
{
    state={
        display:'none',
        rssname:'',
    }
    backtoTop=()=>{
        var timer=setInterval(() => {
            
            var pos = document.body.scrollTop;
            var speed = Math.floor(-pos/3);
            document.body.scrollTop=pos+speed;
            if(pos==0){
                clearInterval(timer);
            }
        }, 30);
    }

    showRss=()=>{
        if(this.state.display=='block'){

            this.setState({display:'none',rssname:'rss'});
        }else{
            this.setState({display:'block',rssname:'rss'});
            var div = document.getElementById('rss');
            var temp =0;
            var timer=setInterval(()=>{
                div.style.transform='scale('+temp+','+temp+')';
                temp+=0.1;
                if(temp>=1){
    
                    clearInterval(timer);
                }
            },30);
        }
      
    }

    render(){
 
        return(
            <Router >
                <div>
                <Layout>
                    <Header style={{backgroundColor:'#fff',height:"190px"}} theme='light'>
                        <Row style={{marginTop:'20px'}}>
                            <Col span={10}>  
                            
                                <img  style={{width:'283px',height:'64px'}} src={headpng}/>
                                <i style ={{color:"#e5e"}}>    专注移动互联网，专注js开发 </i>
                                <div style={{marginTop:'10px'}}>

                                <div className="head-icon">
                                    
                                    <img  style={{width:'70%'}} src={emailpng}/>
                                    <div className="head-icon-font">
                                     联系我 
                                    </div>
                                </div> 
                                <div className="head-icon">
                                <img  style={{width:'70%'}} src={messagepng}/>
                                <div className="head-icon-font">
                                 关于本站  
                                </div>
                                                              
                                </div>

                                <div className="head-icon">
                                <img  style={{width:'70%'}} src={weibopng}/>
                                <div  className="head-icon-font">

                                 我的微博
                                </div>
                                </div>
                                <div className="head-icon">
                                <img  style={{width:'70%'}} src={zhifubaopng}/>
                               <div  className="head-icon-font">
                                打赏本站  
                               </div>
                                </div>

                                </div>
                            </Col>
                            <Col span={14}  style={{textAlign:'right'}}> 
                            
                            <Input  style ={{width:'200px'}} placeholder='please input what you want' />   
                  
                      
                            <Button style={{textAlign:'right'}}> search </Button>
                            
                            <div >
            
                                <span style={{backgroundColor:'#ee0',cursor:'pointer'}} onClick={this.showRss}>&nbsp;&nbsp;&nbsp;&nbsp;订阅rss&nbsp;&nbsp;&nbsp;&nbsp;</span>
                               
                            </div>
                            <div style={{display:this.state.display,width:'300px', height:'100px',float:'right',position:'relative'}}>

<div id="rss" className={this.state.rssname}>

      订阅RSS
</div>


</div>
                        <div>
                            <Carousel vertical autoplay= "true">
                                <div>
                                    <Icon type='notification' theme="twoTone" />写博客不易<a>点击查看详情</a>
                                </div>
                                
                                <div>
                                    <Icon type='notification'/>如果夏天不来，那么还有秋天，有冬天，一定会等到的。
                                </div>
                            </Carousel>
                        </div>
                            </Col>
                        </Row>
                    </Header>

                    <Layout>
                    <Content style={{marginLeft:'3%',paddingRight:'3%'}} >
                    
                    <PageContent />


<Route path="/" exact component={ArticalList}/>

<Route path="/articalpage/:id" exact component={ArticalList}/>

<Route path="/articalpage/jsarticals/:id" component={ArticalList}/>
<Route path="/mybook" component={MyBook}/>
<Route path='/connectme' component={ConnectMe}/>
<Route path='/artical/:id' component={ArticalPage}/>
<Route path='/fileList' component={FileList}/>                                   
<RightContent/>

                    </Content>
                    <Footer>
                        <div style={{borderTopStyle:'solid'}}>
                        <div >
                            <span style={{backgroundColor:'#ff0',cursor:'pointer'}} onClick={this.backtoTop}>返回顶部</span>
                            <span style={{textAlign:'right',float:'right'}}>雨松MOMO</span>
                        </div>
                       
                        </div>
                    </Footer>
                    </Layout>
                </Layout>
                
                  
            </div>
            </Router>

        )
    }
}